import React            from 'react'
import Comment          from './comment'
// import Comment from './test_comment'
import styled, {css}    from 'styled-components'
import size             from 'components/layout/responsive'

export const CommentThread = (props) => {
    const { comments } = props;

    let style = props.child === true ? {display: 'none'} : null;
    if ( props.show) {
        // style = props.child === true ? {marginTop:'40px',paddingLeft: '30px'} : null;
        style = props.child === true ? {paddingLeft: '60px'} : null;
    } 

    // path stores the index path of the recursive structure
    // comment might exist at 0-1-1-2
    // third reply of the second reply of the second reply of the first comment
    // used to make updates to comments in redux store at the right place
    const path = typeof props.path !== 'undefined' ? props.path : [];
    

    return (



        <List child={props.child} show={props.show}>
            { comments.map((comment, i) => {

                let renderReplies = false;
                if ( typeof comment.renderReplies !== 'undefined' && comment.renderReplies === true) {
                    renderReplies = true;
                } 

                comment.path = [...path];
                comment.path.push(i);
                let ref = null;
                const last = i === props.count -1;
                let selected = false;
                 // The below ref is created for comments we may
                // want to scroll to, usually after posting

                if (typeof props.selected !== 'undefined' && comment.id === props.selected) {
                    ref = React.createRef();
                    props.refHandler(ref);
                    selected = true;
                }
                
                return (
                    <Comment 
                        ref = {ref}
                        key = {comment.id} 
                        last = {last} 
                        child = {props.child}
                        selected = {selected}
                        renderReplies = {renderReplies}
                        // scroll = {props.scrollHandler}
                        {...comment}
                        >
                        { comment.replies &&  
                            <CommentThread 
                                path = {comment.path}
                                show = {renderReplies}
                                child = {true} 
                                count = {comment.replies.length}
                                comments = {comment.replies} 
                                selected ={props.selected}
                                refHandler = {props.refHandler}
                                // scrollHandler={props.scrollHandler}
                            />}
                    </Comment>
                )
            })}
        </List>    
    )
} 


const List = styled.ul`

    ${props => props.child && !props.show && css`
        display:none;
    `}

    ${props => props.child && props.show && css`
        padding-left: 60px;
    `}

    ${props => props.child && props.show && size.mobile(`
        padding-left:15px;
    `)}
`

export default CommentThread;