import React, { Component }             from 'react'
import { connect }                      from 'react-redux'
import { createStructuredSelector }     from 'reselect'
import styled, {css}                    from 'styled-components'
// Store
import { selectors, errorTypes }         from 'store/errors/error'

// Components
import Modal                            from 'components/modals/modal'


class Error extends Component {
    
    render() {

        if (!this.props.isOpen) return null;

        return (
            <Modal closeHandler={this.props.hideError}>
                <div>
                    <p>{this.props.error}</p>
                    <Buttons style={{marginTop:"30px"}}>
                        <Button onClick={this.props.hideError}>Dismiss</Button>
                    </Buttons>

                </div>
            </Modal>        
        )
    }
}

const Buttons = styled.div`
    display:flex;
    justify-content:flex-end;
    margin-left:auto;
`

const Button = styled.button`
    display:flex;
    font-size:20px;
    font-weight:600;
    text-transform:uppercase;
    color: black;
    cursor:pointer;
    background:none;
    border:none;
    &:focus {
        outline:0;
    }

    ${props => props.red && css`
        color:#e22f22;
    `}
    ${props => props.bo0ld && css`
        font-weight:700;
    `}

`



const mapStateToProps = createStructuredSelector({
    isOpen: selectors.selectIsOpen,
    error: selectors.selectError
});

const mapDispatchToProps = dispatch => {
    return {
        hideError: () => dispatch({type: errorTypes.HIDE_ERROR}),
    }
};


export default connect(mapStateToProps, mapDispatchToProps)(Error);
