import React, { useState }              from 'react'
import { Formik, Form }                 from 'formik'
import * as Yup                         from 'yup'
import styled, {css}                    from 'styled-components'

// Components
import TextArea                         from 'components/Form/textArea'
import RippleSpinner                    from 'components/spinners/RippleSpinner'
import Modal                            from 'components/modals/modal'
import size                             from 'components/layout/responsive'


const commentForm = React.forwardRef((props, ref) => {
    const { reply_to, path, submitHandler, resetHandler, faqLink } = props;
    const initialValue = typeof props.value !== 'undefined' ? props.value : "";
    const submitLabel = typeof props.submitLabel !== 'undefined' ? props.submitLabel : "Submit my post";

    const [modal, setModal] = useState(false);

    let Cancel = null;
    if (modal) {
        Cancel = <Modal closeHandler={() => setModal(false)}>
            <div style={{width:"90%", margin: "0 auto"}}>
                <p>Are you sure you want to cancel this comment?</p>
                <Buttons style={{marginTop:"30px"}}>
                    <Button large={true} bold={true} red={true} onClick={() => {setModal(false); resetHandler()}}>Yes</Button>
                    <Button large={true} onClick={() => setModal(false)}  style={{marginLeft:"15px"}}>No</Button>
                </Buttons>
            </div>
        </Modal>
    }

    const checkChanged = (e, formik) => {
        const currentValue = e.target.value;
        const initialValue = formik.initialValues.comment;
        if (currentValue !== initialValue) {
            return true;
        } 
        return false;
    }

    return (

    


        <Formik 
            initialValues={{
                comment: initialValue,
                reply_to: reply_to,
                path: path
            }} 
            validationSchema= {Yup.object({
                comment: Yup.string()
                    .required('Required'),
            })} 
            onSubmit={(values, { setSubmitting }) => {

                submitHandler(values).then(() => {
                    setSubmitting(false);
                    resetHandler();
                });
            }}
            onReset={() => {
                setModal(true);
            }}
        >



            {formik => (
                <>
                    {Cancel}

                    <Form>
                        <InputContainer>

                            {formik.isSubmitting &&
                                <SpinnerContainer>
                                    <RippleSpinner />
                                </SpinnerContainer>
                            }

                            <TextArea 
                                id          = "comment"
                                ref         = {ref}
                                name        = "comment" 
                                value       = {formik.values.comment}
                                style       = {{marginBottom:'14px'}}
                                onBlur      = {e => {
                                    formik.touched.comment = checkChanged(e, formik);
                                }}
                                onChange    = {e => {
                                    formik.handleChange(e);
                                    formik.touched.comment = checkChanged(e, formik);
                                }}
                                disabled    = {props.disabled} 
                                data-test   = "commentform"
                                submitting  = {formik.isSubmitting}
                                placeholder = {props.placeHolder}
                            />
                            {/* {formik.errors.comment ? <div>{formik.errors.comment}</div> : null}
                            {formik.errors.article_id ? <div>{formik.errors.article_id}</div> : null}
                            {formik.errors.user_id ? <div>{formik.errors.user_id}</div> : null} */}
                        </InputContainer>
                    </Form>
                
                    <SubmitFooter>
                        <Faq href={faqLink} target="_blank">Questions? Check out our comments FAQ</Faq>
                        {!formik.isSubmitting &&
                            <Buttons>
                                <Button onClick={() => setModal(true)} style={{marginRight:"7px"}}>Cancel</Button>
                                <Button disabled={!formik.touched.comment} red={true}  onClick={(e)=> formik.touched.comment && formik.handleSubmit(e)} data-test="submit">{submitLabel}</Button>
                            </Buttons>
                        }

                    </SubmitFooter>
                </>
            )}

        </Formik>    
    )
})

const InputContainer = styled.div`
    position:relative;
`

const SpinnerContainer = styled.div`
    position:absolute;
    left:50%;
    top:50%;
    transform: translate(-50%, -50%);
`

const Buttons = styled.div`
    display:flex;
    justify-content:flex-end;
    margin-left:auto;
    ${_ => size.mobile(`
        justify-content:space-between;
        margin-left:0;
        width:100%;
    `)}

`


const Button = styled.div`
    display:flex;
    font-size:15px;
    font-weight:600;
    text-transform:uppercase;
    color: black;
    cursor:pointer;
    transition:all 150ms;

    ${props => props.red && css`
        color:#e22f22;
    `}

    ${props => props.large && css`
        font-size:20px;
        /* &:hover {
            font-size:22px;
        } */
    `}
    ${props => props.disabled && css`
        color:#c6c6c7;
        cursor:default;
    `}


`

const SubmitFooter = styled.div` 
    display:flex;
    align-items:center;

    ${_ => size.mobile(`
        flex-direction:column;
    `)}

`
const Faq = styled.a` 
    font-family: "Source sans pro";
    font-size:15px;

    ${_ => size.mobile(`
        display:block;
        font-size:12px;
        border-bottom:1px solid #c6c1c1;
        width: 100%;
        text-align: center;
        padding-bottom: 7px;
        margin-bottom: 8px;
    `)}


`
export default commentForm
