const isset = (p) => {
    if (typeof p !== 'undefined') {
        return true;
    }
    return false;
}
const fetchComments = (commentId = null, path = [], params = {}) => {
    const state = store.getState();
    
    const more = isset(params['more']) && params['more'] === true;
    const sort = state.comments.sort || 'created_at';
    const depth = params.depth || false;
    const limit = state.comments.limit || false;
    const status = state.comments.status || '';
    const toppicks = isset(params['toppicks']) && params['toppicks'] === true;
    const threaded = state.comments.threaded;
    const articleid = state.app.articleId;
    const sortdirection = state.comments.sortDirection || 'asc';

    const reqParams = {articleid,sort,sortdirection,status,threaded};
    
    if (depth) {
        reqParams['depth'] = depth + 1;
        reqParams['depthcondition'] = 'lt';
    }

    let collection = 'article';
    if (commentId !== null) {
        reqParams['commentid'] = commentId;
        // get parents for when loading via url
        if (isset(params.parents) && params.parents === true) {
            reqParams['parents'] = true;
        } else {
            collection = 'replies';
            reqParams['replies'] = true;
        }
        // reqParams['threaded'] = false;

    }
    if (isset(params['threaded'])) {
        reqParams['threaded'] = params['threaded'];
    }

    if (toppicks) {
        reqParams['toppicks'] = true;
    }

    if (isset(params['offset']) && params['offset'] > 0) {
        reqParams['offset'] = params['offset'];
    }

    if (more) {
        reqParams['offset'] = state.comments.totalRootLoaded;
        reqParams['root'] = true;
    }

    if (state.comments.threadLevel > 0 && threaded) {
        reqParams['flattenlevel'] = state.comments.threadLevel;
    }
    


    return dispatch => {

        if (process.env.NODE_ENV === 'development') {
            moxios.wait(function (r) {
                moxios.requests.mostRecent().respondWith(commentData).then(function (r) {
                    updateReduxComments(dispatch, r.data, collection, path, more, commentId);
                });
            });
        }
    


        if (commentId === null) {
                dispatch({
                type: REFRESHING,
                payload: true
            });
        }


        return axios.get('/api/comment?' + qs.stringify(reqParams)).then(r => {
            if (r.data.success === 1) {
                updateReduxComments(dispatch, r.data, collection, path, more, commentId);
            }
        }).catch(e => {
            dispatch({
                type: REFRESHING,
                payload: false
            });
        });
    }
}

const updateReduxComments = (dispatch, data, collection, path, more, commentId) => {
    if (collection === 'article') {
        const totalLoadedRootCount = data.root_comments_count ? parseInt(data.root_comments_count): 0;
        const totalRootCount = data.total_root_count ? parseInt(data.total_root_count): 0;
        const totalPicks = data.pick_count ? parseInt(data.pick_count): 0;
        let total = false;
        if (commentId === null) {
            total = data.total_count ? parseInt(data.total_count): 0;
        }
        const stateUpdate = {
            type: LOAD_COMMENTS,
            append: more ? true : false,
            comments: data.comments,
            totalRootCount: totalRootCount,
            totalPicksLoaded: totalPicks,
            totalLoadedRootCount
        };

        // don't update total when gettting single comment
        if (total) {
            stateUpdate['total'] = total;
        }
        dispatch(stateUpdate);
    } else {
        dispatch({
            type: ADD_REPLIES,
            path,
            comments: data.comments,
            count: data.count
        });
    }

    dispatch({
        type: REFRESHING,
        payload: false
    });
}


