const findCommentByPath = (path, depth, comments) => {

    if (!isset(path)) return null;

    const depthNotExceeded = (i, depth) => {
        if (depth === false || i < depth) {
            return true;
        }
        return false;
    }


    let parent = null;
    for (let i = 0; (i < path.length && depthNotExceeded(i, depth)); i++) {
        if (i === 0) {
            parent = comments[path[i]];
            continue;
        }
        parent = parent.replies[path[i]];
    }

    return parent;
}


const addReply = (comment, state) => {
    const path = comment.path;
    const depth = state.threadLevel;
    const comments = state.comments;
    const parent = findCommentByPath(path, depth, comments);
    const reply_count = parent.replies.length;
    const replies = [...parent.replies];
    replies.splice(reply_count, 0, comment);
    parent.replies = replies;
    parent.renderReplies = true;
    parent.thread_count = ++parent.thread_count;
    parent.reply_count = ++parent.reply_count;
    return state.comments;
};




const INITIAL_STATE = {
    sort: 'created_at',
    limit: 10,
    posted: {
        "success": false,
        "type": ""
    },

    status: 'published',
    refresh: false,
    urlLoad: false,
    comments: [], 
    threaded: true,
    selected: null,
    threadLevel: 1,
    allowComments: true,
    statusPending: false,
    sortDirection:'ASC',

    total: 0,
    totalRootCount: 0,
    totalRootLoaded: 0,
    totalPicksLoaded: 0,
}





const commentsReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {

        case LOGIN_ON_REFRESH: {

            const newState = {
                ...state,
                total : action.commentCount,
                allowComments: action.allowComments
            }

            return newState;
        }
        case LOAD_COMMENTS: {
            const comments = action.append ? state.comments.concat(action.comments) : action.comments;

            const st = {
                ...state,
                comments:   comments,
                totalRootCount:     action.totalRootCount,
            }
            let totalRootLoaded = action.totalLoadedRootCount
            if (action.append) {
                totalRootLoaded = state.totalRootLoaded + action.totalLoadedRootCount;
            } else {
                st.totalPicksLoaded = action.totalPicksLoaded;
            }
            if (action.total) {
                st.total = action.total;
            }

            st.totalRootLoaded = totalRootLoaded;

            return st;
        }
        case ADD_REPLIES: {

            const parent = findCommentByPath(action.path, false, state.comments);
            const replies = action.comments;
            parent.replies = [...parent.replies, ...replies];
            parent.loading = false;
            parent.renderReplies = true;

            return {
                ...state,
                comments: [
                    ...state.comments
                ]
            }
        }
        case POSTING : {

            return {
                ...state,
                posted: {success:false}
            }
        }
        case POST_COMMENT: {
            const comment = {
                ...action.payload.comments,
            }
            const posted = {
                success:true,
                id: comment.id
            };
            let comments = [];

            if (comment.path === null) {
                comments = [...state.comments, comment];
                posted.type = "root";
            } else {
                comments = addReply(comment, state);
                posted.type = "reply";
            }
            const update = {
                ...state,
                comments: [
                    ...comments
                ],
                posted: posted, // used for form element to display response
                selected: comment.id,
                total: ++state.total
            };

            if (comment.path === null) {
                update['totalRoot'] = ++state.totalRoot;
            }

            return update;
        }
        case EDIT_COMMENT: {
            const comment = findCommentByPath(action.path, false, state.comments);
            comment.updatedCommentDateTime = format(new Date(), "yyyy-MM-dd HH:mm:ss"); 
            comment.previous = comment.comment;
            comment.comment = action.comment;
            comment.comment_raw = action.raw;

            return {
                ...state,
                comments: [
                    ...state.comments
                ]
            }
        }

        case COMMENT_STATUS: {
            const comment = findCommentByPath(action.path, false, state.comments);
            comment.status = action.status;

            return {
                ...state,
                comments: [
                    ...state.comments
                ]
            }
        }
        case TOP_PICK: {
            const comment = findCommentByPath(action.path, false, state.comments);
            comment.top_pick = action.pick;
            return {
                ...state,
                comments: [
                    ...state.comments
                ]
            }
        }
        case TOGGLE_LOADING: {
            const comment = findCommentByPath(action.path, false, state.comments);
            if (typeof comment.loading === 'undefined') {
                comment.loading = true;
            } else {
                comment.loading = !comment.loading;
            }

            return {
                ...state,
                comments: [
                    ...state.comments
                ]
            }
        }
        case TOGGLE_SHOW_REPLIES: {
            const comment = findCommentByPath(action.path, false, state.comments);
            comment.renderReplies = !comment.renderReplies;

            return {
                ...state,
                comments: [
                    ...state.comments
                ]
            }
        }
        case THREAD_LEVEL: {
            const threadLevel = parseInt(action.payload);
            return {
                ...state,
                threadLevel
            }
        }
        case LIMIT: {
            const limit = parseInt(action.payload);
            return {
                ...state,
                limit
            }
        }
        case SELECT: {
            const id = parseInt(action.payload);
            return {
                ...state,
                selected: id
            }
        }
        case URL_LOAD: {
            return {
                ...state,
                urlLoad: action.payload
            }
        }
        case THREADED: {
            return {
                ...state,
                threaded: !state.threaded
            }
        }
        case STATUS_PENDING: {
            return {
                ...state,
                statusPending: !state.statusPending
            }
        }
        case UPDATE_STATUS_VIEW: {
            const status = action.payload;
            return {
                ...state,
                status: status
            }
        }

        case GET_PREVIOUS_EDIT: {
            let parent = findCommentByPath(action.path, false, state.comments);
            parent.previous = action.comment;
            return {
                ...state,
                comments: [
                    ...state.comments
                ]
            }
        }
        case REFRESHING: {

            return {
                ...state,
                refresh: action.payload
            }
        }
        case RESET_POSTED_STATUS: {
            return {
                ...state,
                posted: {
                    success: false,
                    type: "",
                    id: null
                }
            }
        }
        default:
            return state;
    }
}





