import React, { Component }             from 'react'
import { connect }                      from 'react-redux'
import styled, {css}                    from 'styled-components'

import { userSelectors }                from 'store/ducks/user'
import { selectors as configSelectors}  from 'store/ducks/config'
import { createStructuredSelector }     from 'reselect'
import { commentCreators, selectors, commentTypes }   from 'store/comments/comments'
import CommentForm                      from 'components/commentForm/commentForm'

// selectCurrentUser
class AddComment extends Component {
    constructor() {
        super();
        this.formInput = React.createRef();

        this.state = {
            disabled: true, // hide the comment form on load
        }
    }

    componentDidMount() {
        if (!this.props.user.commenter && !this.props.user.admin ) {
            this.loadJotForm();
        }
    }


    handleEnable = (e) => {
        const formState = this.state.disabled;
        this.setState({disabled: !formState}, () => {
            if (formState === true) {
                this.formInput.current.focus();
            }
        });
    }


    handleReset = (e, posted) => {
        this.setState({disabled: true});
    }


    canScroll = () => {
        return this.props.newPostRef !== null && typeof this.props.newPostRef.current !== 'undefined' && this.props.newPostRef.current !== null;
    }


    launchJotForm =() => {
    }

    loadJotForm = () => {
        const existingScript = document.getElementById('jotFormApply');
        if (!existingScript) {
            const script = document.createElement('script');
            script.src = 'https://form.jotform.com/static/feedback2.js';
            script.id = 'jotFormApply';
            document.body.appendChild(script);
            script.onload = () => {

                new window.JotformFeedback({ 
                    formId: '202310448358047', 
                    base: 'https://form.jotform.com/', 
                    windowTitle: 'Join the conversation ', 
                    background: '#FFA500', 
                    fontColor: '#FFFFFF', 
                    type: 'false', 
                    height: 500, 
                    width: 700, 
                    openOnLoad: false 
                });
            };
        }
    }



    viewComment = () => {
        this.props.resetPosted();
        this.props.scrollHandler()
    }

    render() {  

        if (!this.props.allowedComments) {
            return <Closed>Closed for comments</Closed>;
        }


        return (
                <SubmitContainer>
                    {( (this.props.user.commenter || this.props.user.admin)) && this.props.user.details &&
                        <Buttons>
                            <Button data-test="enablecomment" onClick={this.handleEnable} disabled={!this.state.disabled}>Post a comment</Button>
                        </Buttons>
                    }

                    {!this.props.user.commenter && !this.props.user.admin &&
                        <Buttons>
                            <Apply onClick={(e) => e.preventDefault()} className="lightbox-202310448358047" href="" target="_blank" data-test="applycomment">Become a commenter</Apply>
                        </Buttons>
                    }

                    {this.state.disabled && this.props.posted.success === true && this.props.posted.type === 'root' && 
                        <>
                            <PostMessageContainer>
                                <Thanks>Thanks {this.props.user.details.firstname}, your comment is live.</Thanks>
                                <Faq href={this.props.faqLink}>Questions? Check out our comments FAQ</Faq>
                            </PostMessageContainer>
                            <Scroll onClick={this.viewComment}>VIEW MY COMMENT</Scroll>
                        </>
                    }

                    {!this.state.disabled && this.props.user.loggedIn &&
                        <Welcome>Hi {this.props.user.details.firstname}, thanks for joining the conversation</Welcome>
                    }

                    <div>
                        {!this.state.disabled && 
                            <CommentForm    
                                ref={this.formInput} 
                                reply_to={false}
                                path={null}
                                faqLink={this.props.faqLink}
                                submitHandler={this.props.postComment}
                                resetHandler={this.handleReset}
                            />
                        }

                    </div>

                </SubmitContainer>

        )
    }
}


const Closed = styled.h3`
    font-family: "Source sans pro";
    font-size: 14px;
    font-weight:700;
    text-transform:uppercase;
    color: #a9a9a9;
    padding-bottom:20px;
    border-bottom: 1px solid #a9a9a9;
    margin-bottom:12px;
`


const SubmitContainer = styled.div` 
    margin-bottom:69px;
`


const Buttons = styled.div`
    display:flex;
    /* justify-content:flex-end; */
    /* margin-left:auto; */
`
const PostMessageContainer = styled.div`
    display:flex;
    justify-content:space-between;
    margin-top:40px;
    margin-bottom:6px;
`

const Thanks = styled.p`
    font-family: "Source sans pro";
    font-size:15px;
    font-weight:600;
`

const Scroll = styled.button`
    color: #e90202;
    font-family: "Source sans pro";
    font-size:15px;
    font-weight:600;
    text-transform:uppercase;
    border:none;
    background:none;
    padding:0;
    cursor: pointer;
    &:focus {
        outline:none;
    }
`

const Faq = styled.a`
    font-family: "Source sans pro";
    font-size:15px;
`


const Button = styled.div`
    font-family: "Source sans pro";
    justify-content:center;
    display:flex;
    flex-basis:100%;
    font-size:14px;
    font-weight:700;
    text-align:center;
    letter-spacing: 0.6px;
    padding:9px;
    text-transform:uppercase;
    cursor:pointer;
    border: 2px solid #e22f22;
    color: black;
    ${props => props.disabled && css`
        border-color: #c6c6c7;
        color: #c6c6c7;
        cursor:unset;
        margin-bottom:31px;

    `}
`

const Apply = styled.a`
    font-family: "Source sans pro";
    justify-content:center;
    display:flex;
    flex-basis:100%;
    font-size:14px;
    font-weight:700;
    text-align:center;
    letter-spacing: 0.6px;
    padding:9px;
    text-transform:uppercase;
    cursor:pointer;
    border: 2px dotted #e90202;
    color: black;
`


const Welcome = styled.p`
    font-family: "Source sans pro";
    font-size:15px;
    font-weight:600;
    margin-bottom:20px;
`


const mapStateToProps = createStructuredSelector({
    user: userSelectors.selectUser,
    posted: selectors.selectPosted,
    faqLink: configSelectors.selectFaqLink,
    allowedComments: selectors.selectAllowedComments,
});

const mapDispatchToProps = dispatch => {
    return {
        postComment: (comment) => dispatch( commentCreators.postComment(comment) ),
        resetPosted: () => dispatch( {type: commentTypes.RESET_POSTED_STATUS})
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(AddComment);