import axios from 'axios'
import Config from '../data/config'
import { createSelector } from 'reselect'


const selectThemeConfig = state => state.config;


const selectFaqLink = createSelector(
    [selectThemeConfig],
    (config) => config.faqLink
);
const selectCongfig = createSelector(
    [selectThemeConfig],
    (config) => config.config
);


const LOAD_CONFIG = 'config/LOAD_CONFIG'
const LOGIN_ON_REFRESH = 'LOGIN_ON_REFRESH'

const loadConfig = () => {

    return dispatch => {
        axios.get('/api/theme/get-config').then(r => {
            console.log(r);
            dispatch({
                type: LOAD_CONFIG,
                payload: r.data,
            });
        });
    }
}


const INITIAL_STATE = {
    config: Config,
    faqLink: ""
}

const configReducer = (state = INITIAL_STATE, action) => {

    switch (action.type) {  
        
        case LOAD_CONFIG: {

            let config = {};
            if (action.payload.data && action.payload.data.comments) {
                config = action.payload.data.comments;
            }

            return {
                ...state,
                config: config
            }
        }

        case LOGIN_ON_REFRESH: {

            return {
                ...state,
                faqLink: action.faqLink,
            }

        }            
        default:
            return state;
    }
}

const selectors = {
    selectFaqLink,
    selectCongfig
}

const configCreators = {
    loadConfig
};

const configTypes = {
    LOAD_CONFIG
};

export {
    configCreators,
    configTypes,
    selectors
};


export default configReducer